	module.exports = {"data":{"markdownRemark":{"html":"<p>The greater than equal to operator (<code>>=</code>) compares the values of two numbers. If the number to the left is greater than or equal to the number to the right, it returns <code>true</code>. Otherwise, it returns <code>false</code>.</p>\n<p>Like the equality operator, greater than equal to operator will convert data types while comparing.</p>\n<h2>Examples</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code> 6  >=  6  // true\n 7  >= '3' // true\n 2  >=  3  // false\n'7' >=  9  // false</code></pre>\n      </div>","fields":{"slug":"/certificates/comparison-with-the-greater-than-or-equal-to-operator/"},"frontmatter":{"title":"Comparison with the Greater Than or Equal to Operator"}}},"pathContext":{"slug":"/certificates/comparison-with-the-greater-than-or-equal-to-operator/"}}

